<template>
  <div name="modal-standard" :id="modalId" ref="root" @keyup.esc="onClose" @keyup.enter="onSubmit" class="custom-salesforce-modal">
    <section role="dialog" :class="'slds-modal slds-fade-in-open slds-modal_' + size || 'medium'" aria-modal="true">
      <div class="slds-modal__container">
        <header class="slds-modal__header" :class="modalHeaderClass">
          <button v-show="btnCloseDisp" class="slds-button slds-button_icon slds-modal__close slds-button_icon-inverse" title="Close" @click="onClose">
            <span class="slds-assistive-text">Close</span>
            <i class="fa fa-times" aria-hidden="true"></i>
          </button>
          <slot name="header"/>
        </header>
        <div class="slds-modal__content slds-p-around_medium">
          <slot name="content"/>
        </div>
        <footer v-if="$slots.footer" class="slds-modal__footer">
          <slot name="footer"/>
        </footer>
      </div>
    </section>
    <div class="slds-backdrop slds-backdrop_open"></div>
  </div>
</template>
<script>

export default {
  name: 'modal-standard',
  props: {
    id: String,
    size: String, // large, medium, small
    btnCloseDisp: { type: Boolean, default: true }
  },
  data () {
    return {
      modalId: 'modal-standard-' + this._uid
    }
  },
  create () {
    this.modalId = this.modalId + this._uid
  },
  mounted () {
    this.$refs.root.focus()
  },
  computed: {
    modalHeaderClass () {
      return [ {'slds-modal__header_empty': !this.$slots.header} ]
    }
  },
  methods: {
    onOpen () {
      let modal = document.getElementById(this.modalId)
      modal.style.display = 'block'
      this.$emit('open')
    },
    onClose () {
      let modal = document.getElementById(this.modalId)
      modal.style.display = 'none'
      this.$emit('close')
    },
    onSubmit () {
      this.$emit('submit')
    }
  }
}
</script>

<style>
  .custom-salesforce-modal {
    display: none;
  }
</style>


