<template>
  <div class="approval-detail">
   <div v-show="emoc.RecordTypeName !== 'Standard'" class="form-padding"> 
      <div class="row" v-show="emoc.Id">
        <!-- <div class="col-sm-12">
          <p class="header-text">Approval Details</p>
        </div> -->
        <div class="col-sm-12">
          <detail-common :emoc="emoc" ref="DetailCommon"/>
        </div>
    </div>
   </div>
  </div>
</template>
<script>
import DetailCommon from './DetailCommom.vue'

export default {
  name: 'approval-detail',
  data () {
    return {}
  },
  watch: {},
  created () {},
  mounted () {
    const queryString = window.location.search
    const urlParams = new URLSearchParams(queryString)
    let obj = {
      Id: urlParams.get('id'),
      sobjecttype: urlParams.get('type')
    }
    if (obj.Id) {
      this.$store.dispatch('receiveApprovalDetail', obj)
    }
  },
  computed: {
    emoc () { return this.$store.state.emoc }
  },
  methods: {},
  components: {
    DetailCommon
  }
}
</script>

<style scoped>
.form-padding {
  /* padding: 14px */
}
.header-text {
  background-color: var(--lwc-colorBackground,rgb(243, 243, 243));
  font-size: 17px;
  padding: 5px 0px 5px 15px;
}
</style>

<style src="../assets/style.css"></style>


