<template>
  <div name="modal-save-sucess" class="modal-save-sucess">
    <modal-std :id="id"
               size="small"
               :btnCloseDisp="false"
               @close="$emit('close')"
               ref="modalAfterSave">
      <template #content>
        <div class="row">
          <div class="col-4 icon-success-block">
            <i class="fa fa-check-circle-o icon-success" aria-hidden="true"></i>
          </div>
          <div class="col-8">
            <span class="message-success">Success!</span><br>
            <span class="message-success-extend">Your submission has been saved!</span>
          </div>
        </div>
      </template>
      <template #footer>
        <button class="slds-button slds-button_neutral" @click="onClose">Close</button>
      </template>
    </modal-std>
  </div>
</template>
<script>
export default {
  name: 'modal-save-sucess',
  props: {
    id: String
  },
  methods: {
    onOpen () {
      this.$refs.modalAfterSave.onOpen()
    },
    onClose () {
      this.$refs.modalAfterSave.onClose()
    }
  }
}
</script>

<style scoped>
  .icon-success-block {
    text-align: right;
  }
  .icon-success {
    padding-right: 25px;
    font-size: 72px;
    color: #4aa54b;
    vertical-align: middle;
  }
  .message-success {
    font-size: 36px;
  }
  .message-success-extend {
    font-size: 18px;
    opacity: 0.5;
  }
</style>



