<template>
  <div name="moc-notification">
      <div class="data-frame">
        <!-- <div class="row top-custom"> -->
          <!-- <div class="col-sm-6">
            <label class="slds-form-element__label" for="text-input-id-1">
              EMOC No.
            </label>
            <br />
            <a class="emoc-link">EMOC-0000341</a>
            <div class="dash-bottom"></div>
          </div> -->
          
        <!-- </div> -->

         <div class="row top-custom">
          <div class="col-sm-6 col-padding-first">
            <input-text-std v-model="emoc.Functional_Location__c" label="Functional Location" disabled></input-text-std>
          </div>
          <div class="col-sm-6 col-padding-first">
            <input-text-std v-model="emoc.Functional_Location_Description__c" label="Functional Location Description" disabled></input-text-std>
          </div>
          <div v-for="(item, idx) in budgetType" :key="idx" v-show="emoc.RecordTypeName === 'Common Workflow' && emoc[item.key] === true" class="col-sm-6 col-padding top-minute">
            <input-text-std v-model="item.value" label="Budget Type" disabled></input-text-std>
          </div>
          <div v-for="item in catagoryFunctional" :key="item.key" v-show="emoc.RecordTypeName === 'Functional Override' && emoc[item.key]" class="col-sm-6 col-padding top-minute">
            <input-text-std v-model="item.value" label="Catagory Type" disabled></input-text-std>
          </div>
          <div v-if="emoc.RecordTypeName === 'Common Workflow'" class="col-sm-6 col-padding top-minute">
            <input-text-std v-model="emoc.Description_of_change__c" label="Description of change" disabled></input-text-std>
          </div>
          <div v-else class="col-sm-6 col-padding top-minute">
            <input-text-std v-model="emoc.Reason_of_Work__c" label="Reason of Work" disabled></input-text-std>
          </div>
        </div>

        <div v-show="emoc.RecordTypeName === 'Common Workflow'" v-for="result in catagoryType" :key="result.key">
          <div v-for="item in result.value" :key="item.key">
            <div v-show="emoc[item.key]" class="row top-custom">
              <div class="col-sm-6 col-padding">
                <input-text-std v-model="result.key" label="Catagory Type" disabled></input-text-std>
              </div>
              <div class="col-sm-6 col-padding">
                <input-text-std v-model="item.value" label="Sub Catagory Type" disabled></input-text-std>
              </div>
            </div>
          </div>
        </div>

        <div class="row top-custom">
          <div class="col-sm-6 col-padding" v-show="emoc.RecordTypeName === 'Common Workflow'">
            <input-text-std v-model="emoc.MOC_Type__c" label="MOC Type" disabled></input-text-std>
          </div>
          <div class="col-sm-6 col-padding" v-show="emoc.RecordTypeName === 'Common Workflow'">
            <input-text-std v-model="emoc.Change_Type__c" label="Change Type" disabled></input-text-std>
          </div>
          <div v-show="emoc.Change_Type__c === 'Permanent'" class="col-sm-6 col-padding top-minute">
            <input-text-std v-model="emoc.Completion_date__c" label="Required Completion date" disabled></input-text-std>
          </div>
          <div v-show="showTypeChangeDate" class="col-sm-6 col-padding top-minute">
            <input-text-std v-model="emoc.Start_date__c" label="Required start date" disabled></input-text-std>
          </div>
          <div v-show="showTypeChangeDate" class="col-sm-6 col-padding top-minute">
            <input-text-std v-model="emoc.End_date__c" label="Required end date" disabled></input-text-std>
          </div>
          <div class="col-sm-6 col-padding top-minute">
            <input-text-std v-model="emoc.RAM_Total__c" label="RAM" disabled></input-text-std>
          </div>
          <div class="col-sm-6 col-padding top-minute" v-show="emoc.RecordTypeName === 'Common Workflow'" >
            <input-currency-std  v-model="emoc.Budget_Estimate_Initial__c"
                            label="Budget Estimate Initial"
                            addonPost="THB"
                            class="input-group padding-t0"
                            disabled>
            </input-currency-std>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
export default {
  name: 'moc-notification',
  props: [
    'emoc'
  ],
  created () {},
  watch: {},
  computed: {
    budgetType () { return this.$store.state.budgetType },
    catagoryType () { return this.$store.state.catagoryType },
    catagoryFunctional () { return this.$store.state.catagoryFunctional },
    showTypeChangeDate () {
      return this.emoc.Change_Type__c === 'Temporary' && (this.emoc.RecordTypeName === 'Common Workflow' || (this.emoc.RecordTypeName === 'Functional Override' && (this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c)))
    }
  },
  methods: {}
}
</script>

<style scoped>
/* .data-frame {
  padding-left: 15px;
} */
.col-padding-first {
  padding: 0px 28px 6px 28px
}
.col-padding {
  padding: 20px 28px 6px 28px
}
.padding-t0 {
  padding-top: 0px;
}
.top-minute {
  margin-top: -10px;
}
.top-custom {
  margin-top: 0px
}
.emoc-link {
  color: green;
  border-bottom: 2px dotted green;
}
.dash-bottom {
  border-bottom: 2px solid;
  margin-top: 5px;
  color: var(--lwc-colorBackground,rgb(243, 243, 243));
}
</style>


