<template>
  <div name="input-currency-standard">
    <div class="slds-form-element" :class="{ 'slds-has-error': error }">
      <label v-show="label" class="slds-form-element__label" id="fixed-text-label" for="text-input-id-1">
        <abbr v-show="required" class="slds-required" title="required">* </abbr>{{label}}
      </label>
      <div class="slds-form-element__control slds-input-has-fixed-addon">
        <!-- <input v-model="$data.$_value" type="text" :name="name" placeholder="" :disabled="disabled" class="slds-input" aria-invalid="true" /> -->
        <span v-show="hasAddonPre" class="slds-form-element__addon" id="fixed-text-addon-pre">{{addonPre}}</span>
        <input v-model="inputNumber"
               class="slds-input"
               :style="addStyle"
               @focusin="setNomalInput()"
               @focusout="setFormatInput()"
               @keyup="calMaxLength()"
               :name="name"
               type="text"
               :onkeypress="'return (event.charCode >= 48 && event.charCode <= 57) || (event.charCode === 46 && \'' + inputNumber + '\'.indexOf(\'.\') === -1) || (event.charCode === 45 && \'' + inputNumber + '\'.indexOf(\'-\') === -1 && event.target.selectionStart === 0)'"
               :id="'currencyInput' + name"
               :disabled="disabled"
               :maxlength="maxLengthInput"
               aria-labelledby="fixed-text-label fixed-text-addon-pre fixed-text-addon-post"
               ref="numberInput" />
        <!-- <button v-show="!disabled && $data.$_value" @click="clearInputText" -->
        <button v-show="!disabled && inputNumber" @click="clearInputText"
                class="slds-button slds-button_icon slds-input__icon slds-input__icon_right icon-clear-data"
                :class="{'icon-clear-data-unit': hasAddonPost && !disabled}"
                title="Clear">
          <i class="fa fa-times-circle" aria-hidden="true"></i>
          <span class="slds-assistive-text">Clear</span>
        </button>
        <span v-show="hasAddonPost" class="slds-form-element__addon" id="fixed-text-addon-post">{{addonPost}}</span>
      </div>
      <div class="slds-form-element__help">
        <slot name="error"/>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'input-currency-standard',
  props: {
    id: String,
    name: String,
    disabled: Boolean,
    error: Boolean,
    inline: Boolean,
    label: String,
    readonly: Boolean,
    required: Boolean,
    value: { type: [String, Number], default: '' },
    helpText: String,
    numLength: { type: Number, default: 17 },
    decimal: { type: Number, default: 2 },
    addStyle: { type: String, default: 'text-align: right;' },
    addonPre: { type: String, default: '' },
    addonPost: { type: String, default: '' }
  },
  data () {
    return {
      inputNumber: this.value.toString(),
      typeInput: 'text',
      maxLengthInput: this.numLength
    }
  },
  created () {
    this.inputNumber = this.numberConvertFormat(this.value, this.decimal)
  },
  computed: {
    hasAddonPre () {
      return this.addonPre !== ''
    },
    hasAddonPost () {
      return this.addonPost !== ''
    }
  },
  watch: {
    'value' () {
      if (this.typeInput === 'text') {
        let value = (this.value + '').replace(' ', '').toString()
        this.inputNumber = this.numberConvertFormat(value, this.decimal)
      } else {
        let numDecimal = this.numberConvertFormat(this.value, this.decimal)
        this.inputNumber = (numDecimal + '').replace(/,/g, '').toString()
      }
    },
    inputNumber () {
      if (this.typeInput === 'number') {
        if (parseFloat(this.inputNumber) === 0 && this.inputNumber.toString().substring(0, 2) !== '0.') {
          this.inputNumber = 0
        }
      }
    }
  },
  methods: {
    clearInputText () {
      this.inputNumber = ''
      this.$emit('input', '')
    },
    setNomalInput () {
      this.inputNumber = (this.inputNumber + '').replace(/,/g, '').toString()
      this.typeInput = 'number'
      this.maxLengthInput = this.numLength
    },
    setFormatInput () {
      this.inputNumber = (this.inputNumber + '').replace(/,/g, '').toString()
      if (this.inputNumber.split('.')[0].length > this.numLength) {
        this.inputNumber = this.inputNumber.substring(0, this.numLength)
      }
      // let numDecimal = this.setToFixed(this.inputNumber, this.decimal).toString()
      let numDecimal = this.inputNumber
      this.typeInput = 'text'
      this.$emit('input', numDecimal)
      this.$emit('changeCurrencyInput', numDecimal)
      this.maxLengthInput = this.numLength
      this.inputNumber = this.numberConvertFormat(this.inputNumber, this.decimal).toString()
    },
    calMaxLength () {
      this.maxLengthInput = 0
      let splitNum = this.inputNumber === 0 ? [0] : this.inputNumber.split('.')
      let numHyphen = this.inputNumber === 0 ? 0 : this.inputNumber.indexOf('-') !== -1 ? 1 : 0
      if ((this.numLength + numHyphen) >= event.target.selectionStart && splitNum[0].length >= event.target.selectionStart && event.key !== '.') {
        if (event.key === '-' && this.inputNumber.indexOf('-') === -1) {
          this.inputNumber = '-' + this.inputNumber
        }
        if (this.inputNumber.indexOf('.') === -1) {
          this.maxLengthInput = this.numLength + numHyphen
        } else {
          this.maxLengthInput = this.numLength + (splitNum[1] !== undefined ? splitNum[1].length : 0) + numHyphen + 1
        }
      } else {
        this.maxLengthInput = this.decimal + splitNum[0].length + 1
        if (event.key === '.' && this.inputNumber.indexOf('.') === -1) {
          this.inputNumber = this.inputNumber + '.'
        }
      }
    },
    numberConvertFormat (numX, decimal = 2) {
      let numberInput = (numX + '').replace(/,/g, '').toString()
      let numberInputConverted
      if (numberInput !== '') {
        let splitInput = numberInput.toString().split('.')
        numberInputConverted = splitInput[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',')
        if (decimal > 0) {
          numberInputConverted += '.' + (splitInput[1] || '0').padEnd(decimal, '0').substring(0, decimal)
        }
      } else {
        numberInputConverted = ''
      }
      return numberInputConverted
    }
  }
}
</script>

<style scoped>
  .slds-input {
    padding-right: 27px;
  }
  .slds-input:disabled {
    padding-right: 10px;
  }
  .icon-clear-data {
    width: 0.875rem;
    height: 0.875rem;
    position: absolute;
    top: 50%;
    margin-top: -0.4375rem;
    line-height: 1;
    border: 0;
    z-index: 2;
    fill: #b0adab;
    font-size: 16px;
    right: 0.50rem;
  }
  .icon-clear-data-unit {
    right: 50px;
  }
  .slds-button_icon {
    color: darkgrey;
  }
  .slds-button_icon:hover {
    color: #0070d2;
  }
</style>


