// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import { store } from './store'
import vuetify from '@/plugins/vuetify'
import VeeValidate from 'vee-validate'
import CheckboxStd from './standardSalesforceCmp/CheckboxStd.vue'
import ModalStd from './standardSalesforceCmp/ModalStd.vue'
import ModalAfterSave from './standardSalesforceCmp/ModalAfterSave.vue'
import ExpandableSectionStd from './standardSalesforceCmp/ExpandableSectionStd.vue'
import PopoverErrorStd from './standardSalesforceCmp/PopoverErrorStd.vue'
import ComboboxStd from './standardSalesforceCmp/ComboboxStd.vue'
import InputTextStd from './standardSalesforceCmp/InputTextStd.vue'
import InputCurrencyStd from './standardSalesforceCmp/InputCurrencyStd.vue'
import TextareaStd from './standardSalesforceCmp/TextareaStd.vue'
import LookupsStd from './standardSalesforceCmp/LookupsStd.vue'
import SelectAction from './standardSalesforceCmp/SelectAction.vue'

Vue.component('checkboxStd', CheckboxStd)
Vue.component('ModalStd', ModalStd)
Vue.component('ModalAfterSave', ModalAfterSave)
Vue.component('ExpandableSectionStd', ExpandableSectionStd)
Vue.component('PopoverErrorStd', PopoverErrorStd)
Vue.component('ComboboxStd', ComboboxStd)
Vue.component('InputTextStd', InputTextStd)
Vue.component('InputCurrencyStd', InputCurrencyStd)
Vue.component('TextareaStd', TextareaStd)
Vue.component('LookupsStd', LookupsStd)
Vue.component('SelectAction', SelectAction)
Vue.use(VeeValidate)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  vuetify,
  store,
  template: '<App/>',
  components: { App }
})



