<template>
  <div name="combobox-standard">
    <div class="slds-form-element" :class="{ 'slds-has-error': error }">
      <label v-show="label" class="slds-form-element__label"><abbr v-show="required" class="slds-required" title="required">* </abbr>{{label}}</label>
      <div class="slds-form-element__control">
        <div v-click-outside="onClickOutside" class="slds-combobox_container">
          <div class="slds-combobox slds-dropdown-trigger slds-dropdown-trigger_click" :class="{ 'slds-is-open': flgOpen }" aria-expanded="false" aria-haspopup="listbox" role="combobox">
            <div class="slds-combobox__form-element slds-input-has-icon slds-input-has-icon_right" role="none">
              <input v-model="textInputValue" :name="name" type="text"
                     class="slds-input slds-combobox__input"
                     @focusin="focusin" autoComplete="off"
                     role="textbox" :disabled="disabled"
                     placeholder="--None--" />
              <span class="slds-icon_container slds-icon-utility-down slds-input__icon slds-input__icon_right" style="top: 10px; z-index: auto;">
                <i class="fa fa-sort-desc" aria-hidden="true" style="font-size: 18px; color: #706e6a;"></i>
              </span>
            </div>
            <div class="slds-dropdown slds-dropdown_fluid" role="listbox">
              <ul class="slds-listbox slds-listbox_vertical" style="padding-left: 0px;" role="presentation">
                <li v-for="option in filterOption" role="presentation" class="slds-listbox__item" @click="setValue(option.value)">
                  <div class="slds-listbox__option slds-listbox__option_plain slds-media_small" style="display: flex;" role="option">
                    <span class="slds-media__figure slds-listbox__option-icon">
                      <span class="slds-icon_container slds-icon-utility-check slds-current-color">
                        <i v-if="value === option.value" class="fa fa-check" style="color: #1d78d3;" aria-hidden="true"></i>
                        <!-- <i class="fa fa-check" style="color: #1d78d3;" aria-hidden="true"></i> -->
                      </span>
                    </span>
                    <span class="slds-media__body">
                      <span class="slds-truncate" :title="option.label">{{option.label}}</span>
                    </span>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="slds-form-element__help">
        <slot name="error"/>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'combobox-standard',
  props: {
    id: String,
    name: String,
    disabled: Boolean,
    error: Boolean,
    inline: Boolean,
    label: String,
    readonly: Boolean,
    required: Boolean,
    value: String,
    helpText: String,
    options: { type: Array, default: [] }
  },
  data () {
    return {
      unique_id: null,
      $_value: this.value,
      flgOpen: false,
      textInputValue: this.value
    }
  },
  mounted () {
    this.unique_id = (typeof this.id !== 'undefined' && this.id !== '') ? this.id : this._uid
  },
  computed: {
    filterOption () {
      return this.options.filter(df => {
        return df.label.indexOf(this.textInputValue) !== -1
      })
    }
  },
  watch: {
    value (value) {
      this.$data.$_value = value
      this.textInputValue = this.getLabelOption(value)
    }
  },
  methods: {
    getLabelOption (value) {
      let label
      let optionFilter = this.options.filter(df => {
        return df.value === value
      })
      if (optionFilter.length > 0) {
        label = optionFilter[0].label
      }
      return label
    },
    focusin () {
      this.textInputValue = ''
      this.flgOpen = true
    },
    onClickOutside () {
      this.textInputValue = this.getLabelOption(this.value || '')
      this.flgOpen = false
    },
    setValue (value) {
      this.$emit('input', value)
      this.textInputValue = this.getLabelOption(value)
      this.flgOpen = false
    }
  }
}
</script>


