import Vue from 'vue'
import Vuex from 'vuex'
import approvalDetail from '../api/approvalDetail'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    budgetType: [
      { key: 'Type_A_Maintenance_budget__c', value: 'Type A (Maintenance budget)' },
      { key: 'Type_B_Operation_budget__c', value: 'Type B (Operation budget)' },
      { key: 'Type_M_Maintenance_project__c', value: 'Type M (Maintenance project budget)' },
      { key: 'Type_P_Plant_change_budget__c', value: 'Type P (Plant change budget)' },
      { key: 'Type_S_Shutdown_budget__c', value: 'Type S (Shutdown budget)' },
      { key: 'Type_V_Un_plan_shutdown_budget__c', value: 'Type V (Un-plan shutdown budget)' }
    ],
    catagoryType: [
      {
        key: 'Mechanical change',
        value: [
          { key: 'Air_Cooled_Exchangers__c', value: 'Air Cooled Exchangers' },
          { key: 'Fired_Heaters_Fire_Boiler__c', value: 'Fired Heaters / Fire Boiler' },
          { key: 'Pipes_Flanges_Valve__c', value: 'Pipes / Flanges / Valve' },
          { key: 'Tracing_steam_Steam_trap__c', value: 'Tracing steam / Steam trap' },
          { key: 'Pressure_Vessels__c', value: 'Pressure Vessels' },
          { key: 'Rotating_equipment__c', value: 'Rotating equipment' },
          { key: 'Relief_Systems__c', value: 'Relief Systems' },
          { key: 'Shell_Tube_Heat_Exchangers__c', value: 'Shell & Tube Heat Exchangers' },
          { key: 'Tanks__c', value: 'Tanks' },
          { key: 'SBM_CBM__c', value: 'SBM / CBM' },
          { key: 'HVAC_Refrigeration__c', value: 'HVAC & Refrigeration' },
          { key: 'Loading_arm_Jetty_Lorry__c', value: 'Loading arm (Jetty & Lorry)' },
          { key: 'Hydraulic_power_unit__c', value: 'Hydraulic power unit' },
          { key: 'Safety_Equipment__c', value: 'Safety Equipment' },
          { key: 'Golden_weld__c', value: 'Golden weld' },
          { key: 'Hot_tap__c', value: 'Hot tap' },
          { key: 'Temporary_repair__c', value: 'Temporary repair' }
        ]
      },
      {
        key: 'Instrument change',
        value: [
          { key: 'Alarm__c', value: 'Alarm' },
          { key: 'APC__c', value: 'APC' },
          { key: 'Base_layer_control_DCS_PLC_TAS_TAM__c', value: 'Base layer control (DCS), PLC, TAS/TAM' },
          { key: 'SIF__c', value: 'SIF' },
          { key: 'Range__c', value: 'Range' },
          { key: 'Control_valve__c', value: 'Control valve' },
          { key: 'Instrumentation_equipment__c', value: 'Instrumentation equipment' },
          { key: 'QMI__c', value: 'QMI' },
          { key: 'Valve__c', value: 'Valve' },
          { key: 'Fire_Gas_detection_system__c', value: 'Fire & Gas detection system' }
        ]
      },
      {
        key: 'Electrical change',
        value: [
          { key: 'Electrical_equipment_system__c', value: 'Electrical equipment / system' },
          { key: 'Emergency_Backup_Systems__c', value: 'Emergency Backup Systems' },
          { key: 'Communication__c', value: 'Communication' },
          { key: 'Driver_Actuator_process_at_Valve__c', value: 'Driver/Actuator (process) at Valve' }
        ]
      },
      {
        key: 'Civil change',
        value: [
          { key: 'Buildling_and_Road__c', value: 'Building and Road' },
          { key: 'Structures_foundations__c', value: 'Structures / foundations' },
          { key: 'Drainage_system_AOC_COC_etc_Bund__c', value: 'Drainage system (AOC, COC, etc) / Bund' },
          { key: 'Refractory_Fireproof__c', value: 'Refractory/Fireproof' },
          { key: 'Insulation__c', value: 'Insulation' },
          { key: 'Painting_and_coating__c', value: 'Painting and coating' }
        ]
      },
      {
        key: 'Catalyst & Chemical',
        value: [
          { key: 'Catalyst_Chemical__c', value: 'Catalyst & Chemical' }
        ]
      },
      {
        key: 'Design limit + IOW change',
        value: [
          { key: 'Design_limit_IOW_change__c', value: 'Design limit + IOW change' }
        ]
      },
      {
        key: 'Tank change service',
        value: [
          { key: 'Tank_change_service__c', value: 'Tank change service' }
        ]
      },
      {
        key: 'Idled / Retired Assets (equipment)',
        value: [
          { key: 'Demolition__c', value: 'Demolition' },
          { key: 'Mothballing__c', value: 'Mothballing' },
          { key: 'Preservation_unit__c', value: 'Preservation unit' }
        ]
      }
    ],
    catagoryFunctional: [
      { key: 'OOS__c', value: 'OOS' },
      { key: 'MOS_for_ops_purpose__c', value: 'MOS for ops purpose' },
      { key: 'Inhibit_for_process_or_Equipment_S_D__c', value: 'Inhibit for process or equipment S/D' },
      { key: 'Inhibit_alarm_for_maintenance__c', value: 'Inhibit Alarm for maintenance' },
      { key: 'Disable_Alarm_by_Ops_for_HW__c', value: 'Disable Alarm by Ops for HW' },
      { key: 'Inhibit_Alarm_request_for_FOX__c', value: 'Inhibit Alarm request for FOX' },
      { key: 'MOS_for_maintenance__c', value: 'MOS for maintenance' },
      { key: 'Safeguarding_force_signal__c', value: 'Safeguarding Force signal' }
    ],
    emoc: {
      Id: '',
      RecordTypeName: '',
      Change_Type__c: '',
      MOS_for_maintenance__c: false,
      Safeguarding_force_signal__c: false
    }
  },
  getters: {},
  mutations: {
    setApprovalDetail: (state, data) => {
      console.log('data: ', data)
      state.emoc = Object.assign(state.emoc, data)
    }
  },
  actions: {
    receiveApprovalDetail: ({commit}, sendParam) => {
      approvalDetail.getDetailApproval(result => {
        commit('setApprovalDetail', result)
      }, sendParam)
    }
  }
})



