<template>
  <div name="checkbox-standard">
    <div class="slds-form-element" :class="{'slds-has-error': error}">
      <div class="slds-form-element__control">
        <div class="slds-checkbox">
          <input type="checkbox" :name="name" :id="unique_id" :disabled="disabled" v-model="$data.$_value" @click="onClick"/>
          <label class="slds-checkbox__label" :for="unique_id">
            <span class="slds-checkbox_faux"></span>
            <span class="slds-form-element__label">{{label}}</span>
          </label>
          <div v-show="helpText && helpText !== ''" class="slds-form-element__icon help-text-icon">
            <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
              <i class="fa fa-info-circle" aria-hidden="true"></i>
              <span class="slds-assistive-text">Help</span>
            </button>
            <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-45px;left:-15px;">
              <div class="slds-popover__body">{{helpText}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="slds-form-element__help">
        <slot name="error"/>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'checkbox-standard',
  props: {
    id: String,
    name: String,
    disabled: Boolean,
    error: Boolean,
    inline: Boolean,
    label: String,
    readonly: Boolean,
    required: Boolean,
    value: Boolean,
    helpText: String
  },
  data () {
    return {
      unique_id: null,
      $_value: this.value
    }
  },
  mounted () {
    this.unique_id = (typeof this.id !== 'undefined' && this.id !== '') ? this.id : this._uid
  },
  watch: {
    value (value) {
      this.$data.$_value = value
    }
  },
  methods: {
    onClick () {
      if (this.disabled || this.readonly) return
      this.$data.$_value = !this.$data.$_value
      this.$emit('input', this.$data.$_value)
    }
  }
}
</script>


