<template>
  <div name="input-text-standard">
    <div class="slds-form-element" :class="{ 'slds-has-error': error }">
      <label v-show="label" class="slds-form-element__label" for="text-input-id-1">
        <abbr v-show="required" class="slds-required" title="required">* </abbr>{{label}}
      </label>
      <div class="slds-form-element__control">
        <input v-model="$data.$_value" type="text" :name="name" placeholder="" :disabled="disabled" class="slds-input" aria-invalid="true" :maxlength="maxlength" />
        <button v-show="!disabled && $data.$_value" @click="clearInputText"
                class="slds-button slds-button_icon slds-input__icon slds-input__icon_right icon-clear-data" title="Clear">
          <i class="fa fa-times-circle" aria-hidden="true"></i>
          <span class="slds-assistive-text">Clear</span>
        </button>
      </div>
      <div class="slds-form-element__help">
        <slot name="error"/>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'input-text-standard',
  props: {
    id: String,
    name: String,
    disabled: Boolean,
    error: Boolean,
    inline: Boolean,
    label: String,
    readonly: Boolean,
    required: Boolean,
    value: { type: [String, Number] },
    helpText: String,
    maxlength: Number
  },
  data () {
    return {
      unique_id: null,
      $_value: this.value
    }
  },
  mounted () {
    this.unique_id = (typeof this.id !== 'undefined' && this.id !== '') ? this.id : this._uid
  },
  watch: {
    value (value) {
      this.$data.$_value = value
    },
    '$data.$_value' (value) {
      this.$emit('input', value)
    }
  },
  methods: {
    clearInputText () {
      this.$data.$_value = ''
      this.$emit('input', '')
    }
  }
}
</script>

<style scoped>
  .icon-clear-data {
    width: 0.875rem;
    height: 0.875rem;
    position: absolute;
    top: 50%;
    margin-top: -0.4375rem;
    line-height: 1;
    border: 0;
    z-index: 2;
    fill: #b0adab;
    font-size: 16px;
    right: 0.50rem;
  }
  .slds-button_icon {
    color: darkgrey;
  }
  .slds-button_icon:hover {
    color: #0070d2;
  }
</style>


