<template>
  <div name="popover-error-std">
    <v-popover
      offset="8"
      placement="top"
      class="popover-error"
      delay="1"
    >
      <i class="fa fa-ban icon-ban-action" aria-hidden="true"></i>
      <template slot="popover">
        <section class="slds-popover slds-popover_error slds-nubbin_bottom-left" role="dialog">
          <button class="slds-button slds-button_icon slds-button_icon-small slds-float_right slds-popover__close slds-button_icon-inverse" title="Close dialog" @click="closePopover">
            <i class="fa fa-times" aria-hidden="true"></i>
            <span class="slds-assistive-text">Close dialog</span>
          </button>
          <header class="slds-popover__header">
            <div class="slds-media slds-media_center slds-has-flexi-truncate ">
              <div class="slds-media__figure">
                <i class="fa fa-ban"></i>
              </div>
              <div class="slds-media__body">
                <h2 class="slds-truncate slds-text-heading_medium" title="Resolve error" style="font-size: 16px;">Please review the following fields</h2>
              </div>
            </div>
          </header>
          <div class="slds-popover__body">
            <!-- <strong>Review the following fields</strong> -->
            <ul class="errorsList slds-list_dotted slds-m-left_medium">
              <li v-for="fieldError in errorBagItems" @click="focusField(fieldError.field)">
                <a>{{getLabelFields(fieldError.field)}}</a>
              </li>
            </ul>
          </div>
        </section>
      </template>
    </v-popover>
  </div>
</template>
<script>
import { VPopover } from 'v-tooltip'

export default {
  name: 'popover-error-std',
  props: [
    'errorBagItems',
    'getLabelFields',
    'focusField'
  ],
  methods: {
    closePopover () {
      document.getElementsByClassName('trigger')[0].click()
    }
  },
  components: {
    VPopover
  }
}
</script>

<style scoped>
  .icon-ban-action {
    font-size:24px;
    vertical-align: middle;
    color: rgb(234, 0, 30);
  }
</style>


