<template>
  <div name="textarea-standard">
    <div class="slds-form-element" :class="{ 'slds-has-error': error }">
      <label v-show="label" class="slds-form-element__label" for="text-input-id-1">
        <abbr v-show="required" class="slds-required" title="required">* </abbr>{{label}}
      </label>
      <div class="slds-form-element__control">
        <textarea v-model="$data.$_value" :name="name" placeholder="" :disabled="disabled" :rows="rows" class="slds-textarea"></textarea>
      </div>
      <div class="slds-form-element__help">
        <slot name="error"/>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'textarea-standard',
  props: {
    id: String,
    name: String,
    disabled: Boolean,
    error: Boolean,
    inline: Boolean,
    label: String,
    readonly: Boolean,
    required: Boolean,
    value: String,
    helpText: String,
    rows: Number
  },
  data () {
    return {
      unique_id: null,
      $_value: this.value
    }
  },
  mounted () {
    this.unique_id = (typeof this.id !== 'undefined' && this.id !== '') ? this.id : this._uid
  },
  watch: {
    value (value) {
      this.$data.$_value = value
    },
    '$data.$_value' (value) {
      this.$emit('input', value)
    }
  },
  methods: {
    clearInputText () {
      this.$data.$_value = ''
      this.$emit('input', '')
    }
  }
}
</script>

<style scoped>
  .icon-clear-data {
    width: 0.875rem;
    height: 0.875rem;
    position: absolute;
    top: 50%;
    margin-top: -0.4375rem;
    line-height: 1;
    border: 0;
    z-index: 2;
    fill: #b0adab;
    font-size: 16px;
    right: 0.50rem;
  }
  .slds-button_icon {
    color: darkgrey;
  }
  .slds-button_icon:hover {
    color: #0070d2;
  }
</style>


