<template>
  <div name="lookup-standard">
    <div class="slds-form-element" :class="{ 'slds-has-error': error }">
      <label v-show="label" class="slds-form-element__label"><abbr v-show="required" class="slds-required" title="required">* </abbr>{{label}}</label>
      <div class="slds-form-element__control">
        <div v-click-outside="onClickOutside" class="slds-combobox_container">
          <div class="slds-combobox slds-dropdown-trigger slds-dropdown-trigger_click" :class="{ 'slds-is-open': flgOpen }" aria-expanded="true" aria-haspopup="listbox" role="combobox">
            <div class="slds-combobox__form-element slds-input-has-icon slds-input-has-icon_right" role="none">
              <span v-if="isValue" class="slds-icon_container slds-combobox__input-entity-icon" :class="'slds-icon-standard-' + getIconOption(value)">
                <img :src="require('../assets/iconStd/' + getIconOption(value) + '_120.png')"/>
              </span>
              <input v-model="textInputValue" :name="name" type="text"
                     class="slds-input slds-combobox__input" :class="{'slds-has-focus': flgOpen, 'padding-icon': getIconOption(value), 'disabled-click-input-text': value && !disabled}"
                     @focusin="onfocusin" autoComplete="off"
                     @input="onInput"
                     role="textbox" :disabled="disabled || isValue"
                     placeholder="Search..." />
              <button v-if="isValue && !disabled" class="slds-button slds-button_icon slds-input__icon slds-input__icon_right icon-clear-data"
                            title="Remove selected option">
                <i class="fa fa-times" aria-hidden="true" @click="clearData"></i>
                <span class="slds-assistive-text">Remove selected option</span>
              </button>
              <span v-if="!isValue && !disabled" class="slds-icon_container slds-icon-utility-search slds-input__icon slds-input__icon_right icon-search">
                <i class="fa fa-search" aria-hidden="true"></i>
              </span>
            </div>
            <div class="slds-dropdown slds-dropdown_length-with-icon-7 slds-dropdown_fluid" role="listbox">
              <ul class="slds-listbox slds-listbox_vertical ul-clear-padding-left-std" role="presentation">
                <li role="presentation" class="slds-listbox__item" @click="showPopupAllResults">
                  <div class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_term slds-has-focus" role="option">
                    <span class="slds-media__figure slds-listbox__option-icon">
                      <span class="slds-icon_container slds-icon-utility-search" title="Search for term: ">
                        <i class="fa fa-search show-all-results-icon" aria-hidden="true"></i>
                        <span class="slds-assistive-text">Show All Results for "{{textInputValue}}"</span>
                      </span>
                    </span>
                    <span class="slds-media__body">
                      <span class="slds-listbox__option-text slds-listbox__option-text_entity">Show All Results for "{{textInputValue}}"</span>
                    </span>
                  </div>
                </li>
                <li v-for="option in optionsLimit" role="presentation" class="slds-listbox__item" @click="setValue(option.value)">
                  <div class="slds-media slds-listbox__option slds-listbox__option_entity slds-listbox__option_has-meta" role="option">
                    <span class="slds-media__figure slds-listbox__option-icon">
                      <span class="slds-icon_container" :class="'slds-icon-standard-' + option.icon">
                        <img :src="require('../assets/iconStd/' + option.icon + '_120.png')"/>
                      </span>
                    </span>
                    <span class="slds-media__body">
                      <span class="slds-listbox__option-text slds-listbox__option-text_entity">{{option.label}}</span>
                      <span v-show="option.meta" class="slds-listbox__option-meta slds-listbox__option-meta_entity">{{option.meta}}</span>
                    </span>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="slds-form-element__help">
        <slot name="error"/>
      </div>
    </div>
    <modal-std v-if="reRenderModal"
               size="medium" ref="refShowAllResultsPopup"
               @close="onClosePopup"
               id="showAllResultsPopup">
      <template #header>
        <span class="slds-card__header-link slds-text-heading_small">{{label}}</span>
      </template>
      <template #content>
        <div class="row">
          <div class="col-md-12">
            <label class="slds-form-element__label">Search</label>
            <div class="slds-form-element__control">
              <input v-model="textInputValue" :name="name" type="text" @input="onInput"
                      class="slds-input input-search" />
              <i class="fa fa-search popup-icon-search" aria-hidden="true"></i>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <div v-if="options.length > 0" name="scrollBarTableLookup" @scroll="onScrollTable" class="scrollbar-table">
              <table name="tableLookupShowAllResult" class="table-show-all-results-lookup">
                <tr>
                  <th width="5%">#</th>
                  <th width="40%">
                    <span class="table-lookup-header" @click="clickSort('label')">{{label}}
                      <i v-show="sortLabel === 'none'" class="fa fa-sort" aria-hidden="true"></i>
                      <i v-show="sortLabel === 'asc'" class="fa fa-sort-asc" aria-hidden="true"></i>
                      <i v-show="sortLabel === 'desc'" class="fa fa-sort-desc" aria-hidden="true"></i>
                    </span>
                  </th>
                  <th v-show="showDetailTable" width="60%">
                    <span class="table-lookup-header" @click="clickSort('meta')">Detail
                      <i v-show="sortMeta === 'none'" class="fa fa-sort" aria-hidden="true"></i>
                      <i v-show="sortMeta === 'asc'" class="fa fa-sort-asc" aria-hidden="true"></i>
                      <i v-show="sortMeta === 'desc'" class="fa fa-sort-desc" aria-hidden="true"></i>
                    </span>
                  </th>
                </tr>
                <tr v-for="(option, idx) in dataRow">
                  <td>{{idx + 1}}</td>
                  <td><span @click="setValueClosePopup(option.value)" class="label-lookup-popup">{{option.label}}</span></td>
                  <td v-show="showDetailTable">{{option.meta}}</td>
                </tr>
              </table>
            </div>
            <div v-else class="no-data-found-lookup">
              <div class="icon-no-data-result"><i class="fa fa-search" aria-hidden="true"></i></div>
              No results for "{{textInputValue}}" in {{label}}
            </div>
          </div>
        </div>
      </template>
      <template #footer>
        <div style="text-align: right;">
          <button class="slds-button slds-button_neutral" @click="callClosePopup">Cancel</button>
        </div>
      </template>
    </modal-std>
  </div>
</template>
<script>

export default {
  name: 'lookup-standard',
  props: {
    id: String,
    name: String,
    disabled: Boolean,
    error: Boolean,
    inline: Boolean,
    label: String,
    readonly: Boolean,
    required: Boolean,
    value: String,
    helpText: String,
    icon: String,
    options: { type: Array, default: [] },
    limit: { type: Number, default: 5 }
  },
  data () {
    return {
      unique_id: null,
      $_value: this.value,
      flgOpen: false,
      textInputValue: this.getLabelOption(this.value),
      sortLabel: 'none',
      sortMeta: 'meta',
      maximumRow: 50,
      reRenderModal: true
    }
  },
  mounted () {
    this.unique_id = (typeof this.id !== 'undefined' && this.id !== '') ? this.id : this._uid
  },
  computed: {
    isValue () {
      return this.value !== '' && this.value
    },
    optionsLimit () {
      return this.options.slice(0, this.limit)
    },
    showDetailTable () {
      let isShow = false
      if (this.options.length > 0) {
        if (this.options.filter(df => {
          return df.meta !== null && typeof df.meta !== 'undefined' && df.meta !== ''
        }).length > 0) {
          isShow = true
        }
      }
      return isShow
    },
    fetchDataRow () {
      return this.options.slice(0, this.maximumRow)
    },
    dataRow () {
      return this.fetchDataRow.sort((a, b) => {
        if (this.sortLabel === 'asc') {
          return (a.label > b.label) ? 1 : ((b.label > a.label) ? -1 : 0)
        } else if (this.sortLabel === 'desc') {
          return (b.label > a.label) ? 1 : ((a.label > b.label) ? -1 : 0)
        }
      })
    }
  },
  watch: {
    value (value) {
      this.$data.$_value = value
      this.textInputValue = this.isValue ? this.getLabelOption(value) : ''
    },
    options: {
      handler (newValue, oldValue) {
        if (newValue.length > 0) {
          let filter = newValue.filter(df => {
            return df.value === this.value
          })
          if (filter.length > 0) {
            this.textInputValue = filter[0].label
          }
        }
      },
      deep: true
    }
  },
  methods: {
    onfocusin () {
      this.textInputValue = ''
      this.flgOpen = true
    },
    onInput () {
      this.$emit('search', event.target.value)
    },
    onClickOutside () {
      this.flgOpen = false
    },
    setValue (value) {
      this.$emit('input', value)
      this.flgOpen = false
    },
    clearData () {
      this.$emit('input', '')
      this.textInputValue = ''
    },
    getLabelOption (value) {
      let label = ''
      let filter = this.options.filter(df => {
        return df.value === value
      })
      if (filter.length > 0) {
        label = filter[0].label ? filter[0].label : ''
      }
      return label
    },
    getIconOption (value) {
      let icon = ''
      if (this.icon !== '' && this.icon) {
        icon = this.icon
      } else {
        let filter = this.options.filter(df => {
          return df.value === value
        })
        if (filter.length > 0) {
          icon = filter[0].icon ? filter[0].icon : ''
        }
      }
      return icon
    },
    showPopupAllResults () {
      this.$refs.refShowAllResultsPopup.onOpen()
    },
    onClosePopup () {
      this.sortLabel = 'none'
      this.maximumRow = 50
      this.reRenderModal = false
      this.$nextTick(() => {
        this.reRenderModal = true
      })
    },
    callClosePopup () {
      this.$nextTick(() => {
        this.$refs.refShowAllResultsPopup.onClose()
      })
    },
    setValueClosePopup (value) {
      this.setValue(value)
      this.callClosePopup()
    },
    clickSort (columnSort) {
      if (columnSort === 'label') {
        if (this.sortLabel === 'none' || this.sortLabel === 'desc') {
          this.sortLabel = 'asc'
        } else {
          this.sortLabel = 'desc'
        }
      } else {
        if (this.sortMeta === 'none' || this.sortMeta === 'desc') {
          this.sortMeta = 'asc'
        } else {
          this.sortMeta = 'desc'
        }
      }
    },
    onScrollTable () {
      let scrollTop = document.getElementsByName('scrollBarTableLookup')[0].scrollTop
      let offsetHeight = document.getElementsByName('scrollBarTableLookup')[0].offsetHeight
      let scrollHeight = document.getElementsByName('tableLookupShowAllResult')[0].scrollHeight
      if (scrollTop + offsetHeight >= scrollHeight && this.maximumRow < this.options.length) {
        this.maximumRow += 50
      }
    }
  }
}
</script>

<style scoped>
  .lookup-icon-list {
    width: 10px;
  }
  .padding-icon {
    padding-left: 30px;
  }
  .icon-clear-data {
    right: 0.5rem;
    font-size: 16px; color: darkgrey;
  }
  .icon-clear-data:hover {
    color: #0070d2;
  }
  .icon-search {
    right: 0.5rem;
    top: 45%;
    font-size: 16px; color: darkgrey;
  }
  .ul-clear-padding-left-std {
    padding-left: 0px;
  }
  .disabled-click-input-text {
    background-color: #ffffff;
    cursor: default;
  }
  .show-all-results-icon {
    font-size: 16px; color: darkgrey;
  }
  .popup-icon-search {
    font-size: 14px;
    color: darkgrey;
    right: 26px;
    position: relative;
  }
  .input-search {
    display: inline-block;
    width: 40%;
    min-width: 200px;
  }
  .table-lookup-header:hover {
    cursor: pointer;
    /* text-decoration: underline; */
  }
  .table-show-all-results-lookup td, .table-show-all-results-lookup th {
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    padding: 5px;
    padding-left: 15px;
    padding-right: 15px;
  }
  .table-show-all-results-lookup th {
    background-color: #fafaf9;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 15px;
    padding-right: 15px;
  }
  .table-show-all-results-lookup tr:hover {background-color: #f2f2f2;}
  .scrollbar-table {
    overflow: scroll;
    height: calc(100vh - 353px);
  }
  .label-lookup-popup {
    color: #4282d7;
  }
  .label-lookup-popup:hover {
    cursor: pointer;
    text-decoration: underline;
  }
  .no-data-found-lookup {
    text-align: center;
    font-size: x-large;
  }
  .icon-no-data-result {
    text-align: center;
    font-size: xxx-large;
    color: #0176d4;
  }
</style>


